<template>
  <view class="container">
    <image src="../../static/loginLogo.jpg" mode="widthFix" style="width: 200px;"></image>
    <view class="title-box">
      {{userType}}登录
    </view>
    <view class="login-box">
      <view class="login-item">
        <!-- 用户类型 -->
        <uni-icons type="auth" size="30" color="#858585"></uni-icons>
        <picker style="margin-left: 10px;" @change="bindPickerChange" :value="index" :range="userArr">{{userArr[index]}}
        </picker>
      </view>
      <view class="login-item">
        <!-- 账号 -->
        <uni-icons type="person" size="30" color="#858585"></uni-icons>
        <input class="input-item" type="number" :placeholder="placeholder" maxlength="12" v-model="id" />
      </view>
      <view class="login-item">
        <!-- 密码 -->
        <uni-icons type="locked" size="30" color="#858585"></uni-icons>
        <input class="input-item" password placeholder="请输入密码" v-model="passWord" />
      </view>
      <button class="btn-login" @click="login">登录</button>
      <view class="forget">
        忘记密码
      </view>
    </view>
  </view>
</template>

<script>
  // 按需从 vuex 中导入 mapState 辅助函数
  import {
    mapState,
    mapMutations
  } from 'vuex'
  export default {
    data() {
      return {
        // 用户类型
        userType: '学生',
        placeholder: '请输入学号',
        // 用户类型数组
        userArr: ['学生', '班主任', '管理员'],
        index: 0,
        // 账号
        id: '',
        // 密码
        passWord: ''
      };
    },
    computed: {
      // token 是用户登录成功之后的 token 字符串
      ...mapState('user', ['token']),
    },
    methods: {
      bindPickerChange: function(e) {
        this.index = e.detail.value
        this.userType = this.userArr[this.index]
        if (this.userType === "学生") {
          this.placeholder = "请输入学号"
        } else {
          this.placeholder = "请输入职工号"
        }
      },
      // 使用 mapMutations 辅助方法，把 user 模块中的 updateToken 方法映射到当前组件中使用
      ...mapMutations('user', ['updateType', 'updateToken']),
      async login() {
        const res = await uni.$http.get('/api/login', {
          userType: this.userType,
          uid: this.id,
          upwd: this.passWord
        })
        // 请求失败
        if (res.statusCode !== 200) return uni.$showMsg()
        console.log(res.data);
        if (res.data.status === 0) {
          // 更新 vuex 中的 token
          this.updateToken(res.data.token)
          this.updateType(this.userType)
          uni.switchTab({
            url: '/pages/home/home',
            // switchTab成功跳转后调用success,此时可以拿到跳转后页面的page对象,从而调用页面onLoad方法重载页面
            success: function(e) {
              var page = getCurrentPages().pop();
              if (page == undefined || page == null) return;
              page.onLoad();
            }
          })
        } else {
          uni.showToast({
            title: res.data.message,
            icon: 'none'
          })
        }
        // uni.request({
        //   url: 'http://192.168.1.11:5000/api/login',
        //   method: 'POST',
        //   data: {
        //     userType: this.userType,
        //     uid: this.id,
        //     upwd: this.passWord
        //   },
        //   header: {
        //     // 对于 POST 方法且 header['content-type'] 为 application/x-www-form-urlencoded 的数据，会将数据转换为 query string
        //     'content-type': 'application/x-www-form-urlencoded'
        //   },
        //   success: (res) => {
        //     console.log(res.data);
        //     if (res.data.status === 0) {
        //       // 更新 vuex 中的 token
        //       this.updateToken(res.data.token)
        //       this.updateType(this.userType)
        //       uni.switchTab({
        //         url: '/pages/home/home',
        //         // switchTab成功跳转后调用success,此时可以拿到跳转后页面的page对象,从而调用页面onLoad方法重载页面
        //         success: function(e) {
        //           var page = getCurrentPages().pop();
        //           if (page == undefined || page == null) return;
        //           page.onLoad();
        //         }
        //       })
        //     } else {
        //       uni.showToast({
        //         title: res.data.message,
        //         icon: 'none'
        //       })
        //     }
        //   }
        // });
      }
    }
  }
</script>

<style lang="scss" scoped>
  // 最外层容器
  .container {
    text-align: center;
  }

  // 标题 “学生登录”
  .title-box {
    font-size: 22px;
    margin: 10px;
    color: #9eb2f7;
  }

  // 登录表单项的外层容器
  .login-box {
    margin: 0 30px;
  }

  // 每个登录表单项的容器
  .login-item {
    padding: 10px 10px 0;
    display: flex;
    align-items: center;
    border-bottom: solid 1px #999;
  }

  // 输入框
  .input-item {
    width: 69%;
    height: 28px;
    margin: 10px;
    text-align: left;
  }

  // 登录按钮
  .btn-login {
    width: 256px;
    margin: 30px auto 14px;
    border-radius: 18px;
    background-color: #c8d9ff;
  }

  // 忘记密码
  .forget {
    color: #7979f5;
    font-size: 16px;
  }
</style>
